<template>
  <div id="calc-books">
    <v-tabs
      v-model="tab"
      fixed-tabs
    >
      <v-tab>Issei</v-tab>
      <v-tab>Masami</v-tab>
    </v-tabs>
    
    <v-data-table
      mobile-breakpoint="0"
      :headers="headers"
      :items="this.selectData.table"
      :sort-by="['date']"
      :sort-desc="[true]"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>合計：{{ selectData.total }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn @click.stop="freeIncomeDialog=true">フリー</v-btn>
          <v-btn @click.stop="moveIncomeDialog=true">移動</v-btn>
          <v-btn @click.stop="outgoDialog=true">支出</v-btn>
        </v-toolbar>
      </template>
    </v-data-table>
    
    <v-dialog
      v-model="freeIncomeDialog"
    >
      <v-card>
        <v-card-title>★収入（フリー入力）</v-card-title>
        <v-row justify="center" align-content="center" class="pt-5">
          <v-col cols="4" class="pa-0 d-flex justify-center align-center">変更前：</v-col>
          <v-col cols="4" class="pa-0">{{ selectData.total }} 円</v-col>
        </v-row>
        <v-row justify="center" align-content="center">
          <v-col cols="4" class="pa-0 d-flex justify-center align-center">追加金額：</v-col>
          <v-col cols="4" class="pa-0">
            <v-text-field
              v-model="add"
              suffix="円"
              single-line
              type="number"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="mt-0">
          <v-spacer></v-spacer>
          <v-col cols="10" class="pt-0">
            <v-divider></v-divider>
            <v-divider></v-divider>
            <v-divider></v-divider>
            <v-divider></v-divider>
          </v-col>
        </v-row>
        <v-row justify="center" align-content="center" class="pb-5">
          <v-col cols="4" class="pa-0 d-flex justify-center align-center">変更後：</v-col>
          <v-col cols="4" class="pa-0">{{ selectData.total + Number(add) }} 円</v-col>
        </v-row>
        <v-row class="pb-3">
          <v-spacer></v-spacer>
          <v-col cols="2" class="pa-0">
            <v-btn
              color="primary"
              @click="clickSubmitFreeIncome()"
            >確定</v-btn>
          </v-col>
          <v-col cols="3" class="pa-0 mr-3">
            <v-btn @click="add = null">クリア</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
    
    <v-dialog
      v-model="moveIncomeDialog"
    >
      <v-card class="pa-3">
        <v-card-title class="pl-0 pr-0" justify-content="center">★収入（洗い物小遣いから移動）</v-card-title>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="5" class="pr-0">
            <v-subheader class="text-body-1">変更前</v-subheader>
          </v-col>
          <v-col cols="7" class="pl-0">
            <v-text-field
              v-model="selectData.total"
              dense
              outlined
              hide-details="false"
              disabled
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="5" class="pr-0">
            <v-subheader class="text-body-1">洗い物小遣い</v-subheader>
          </v-col>
          <v-col cols="5" class="pl-0">
            <v-text-field
              :value="selectData.total_wash - Number(add_part)"
              dense
              outlined
              hide-details="false"
              disabled
            ></v-text-field>
          </v-col>
          <v-col cols="2" class="pl-0">
            <v-btn
              class="pa-0"
              color="primary"
              @click="add_part=0;add=selectData.total_wash;"
            >全額</v-btn>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="5" class="pr-0">
            <v-subheader class="text-body-1">一部入金</v-subheader>
          </v-col>
          <v-col cols="7" class="pl-0">
            <v-text-field
              v-model="add_part"
              dense
              outlined
              hide-details="false"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="5" class="pr-0">
            <v-subheader class="text-body-1">変更後</v-subheader>
          </v-col>
          <v-col cols="7" class="pl-0">
            <v-text-field
              :value="selectData.total + Number(add)"
              dense
              outlined
              hide-details="false"
              disabled
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="pb-3">
          <v-spacer></v-spacer>
          <v-col cols="4" class="pa-0">
            <v-btn
              color="primary"
              @click="clickSubmitMoveIncome()"
            >確定</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
    
    <v-dialog
      v-model="outgoDialog"
    >
      <v-card>
        <v-card-title>★支出（フリー入力）</v-card-title>
        <v-row justify="center" align-content="center" class="pt-5">
          <v-col cols="4" class="pa-0 d-flex justify-center align-center">変更前：</v-col>
          <v-col cols="4" class="pa-0">{{ selectData.total }} 円</v-col>
        </v-row>
        <v-row justify="center" align-content="center">
          <v-col cols="4" class="pa-0 d-flex justify-center align-center">削減金額：</v-col>
          <v-col cols="4" class="pa-0">
            <v-text-field
              v-model="remove"
              prefix="-"
              suffix="円"
              single-line
              type="number"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="mt-0">
          <v-spacer></v-spacer>
          <v-col cols="10" class="pt-0">
            <v-divider></v-divider>
            <v-divider></v-divider>
            <v-divider></v-divider>
            <v-divider></v-divider>
          </v-col>
        </v-row>
        <v-row justify="center" align-content="center" class="pb-5">
          <v-col cols="4" class="pa-0 d-flex justify-center align-center">変更後：</v-col>
          <v-col cols="4" class="pa-0">{{ selectData.total - Number(remove) }} 円</v-col>
        </v-row>
        <v-row class="pb-3">
          <v-spacer></v-spacer>
          <v-col cols="2" class="pa-0">
            <v-btn
              color="primary"
              @click="clickSubmitOutgo()"
            >確定</v-btn>
          </v-col>
          <v-col cols="3" class="pa-0 mr-3">
            <v-btn @click="remove = null">クリア</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { API, graphqlOperation } from 'aws-amplify';
import { listPersonals, listBooks } from '@/graphql/queries';
import { updatePersonal, createBooks } from '@/graphql/mutations';

export default {
  name: "CalcBooks",
  
  data() {
    return {
      tab: null,
      freeIncomeDialog: false,
      outgoDialog: false,
      moveIncomeDialog: false,
      
      add: null,
      add_part: null,
      remove: null,
      
      headers: [
        {
          text: '追加日',
          align: 'start',
          value: 'date'
        },
        { text: '追加金額', value: 'fee' }
      ],
      
      selectData: {
        total: null,
        table: []
      },
      issei: {
        id: null,
        name: 'is',
        isCheck: false,
        total: null,
        total_wash: null,
        table: []
      },
      masami: {
        id: null,
        name: 'ms',
        isCheck: true,
        total: null,
        total_wash: null,
        table: []
      },
      // addData: 0
    };
  },
  
  created: async function() {
    await API.graphql({
      query: listPersonals
    })
    .then((result) => {
      console.log(result);
      result.data.listPersonals.items.forEach(item => {
        if(item.name == 'is') {
          this.issei.id = item.id;
          this.issei.total = item.total;
          this.issei.total_wash = item.total_wash;
        }
        else if(item.name == 'ms') {
          this.masami.id = item.id;
          this.masami.total = item.total;
          this.masami.total_wash = item.total_wash;
        }
      });
    })
    .catch((error) => {
      console.log(error);
    });
    
    await this.setTable();
    // await API.graphql({
    //   query: listBooks
    // })
    // .then((result) => {
    //   console.log(result);
    //   result.data.listBooks.items.forEach(item => {
    //     if(item.personal_name == 'is') {
    //       this.issei.table.push({
    //         date: item.register_date,
    //         fee: item.register_fee
    //       });
    //     }
    //     else if(item.personal_name == 'ms') {
    //       this.masami.table.push({
    //         date: item.register_date,
    //         fee: item.register_fee
    //       });
    //     }
    //   });
    // })
    // .catch((error) => {
    //   console.log(error);
    // });
  },
  
  computed: {
    tableItems() {
      let items=[];
      
      this.selectData.table.forEach(item => {
        items.push(item);
      });
      
      return items;
    }
  },
  
  methods: {
    dateToStr24HPad0(date, format) {
      if (!format) {
          format = 'YYYY/MM/DD hh:mm:ss'
      }
      format = format.replace(/YYYY/g, date.getFullYear());
      format = format.replace(/MM/g, ('0' + (date.getMonth() + 1)).slice(-2));
      format = format.replace(/DD/g, ('0' + date.getDate()).slice(-2));
      format = format.replace(/hh/g, ('0' + date.getHours()).slice(-2));
      format = format.replace(/mm/g, ('0' + date.getMinutes()).slice(-2));
      format = format.replace(/ss/g, ('0' + date.getSeconds()).slice(-2));
      return format;
    },

    formatDate(dateStr) {
      let dateTokyo = (new Date(dateStr)).toLocaleString({timeZone: 'Asia/Tokyo'});
      let date = this.dateToStr24HPad0(new Date(dateTokyo));
      return date;
    },
    
    // 使っていない
    async setTable_old() {
      await API.graphql({
        query: listBooks
      })
      .then((result) => {
        console.log(result);
        
        this.issei.table=[];
        this.masami.table=[];
        
        result.data.listBooks.items.forEach(item => {
          if(item.personal_name == 'is') {
            this.issei.table.push({
              // date: item.register_date,
              date: this.formatDate(item.register_date),
              fee: item.register_fee
            });
          }
          else if(item.personal_name == 'ms') {
            this.masami.table.push({
              // date: item.register_date,
              date: this.formatDate(item.register_date),
              fee: item.register_fee
            });
          }
        });
      })
      .catch((error) => {
        console.log(error);
      });      
    },

    async setTable() {
      let books = await API.graphql(graphqlOperation(listBooks));
      let items = books.data.listBooks.items;
      let nextToken = books.data.listBooks.nextToken;
      while(nextToken) {
        books = await API.graphql(graphqlOperation(listBooks,{nextToken:nextToken}));
        items = items.concat(books.data.listBooks.items);
        nextToken = books.data.listBooks.nextToken;
      }

      this.issei.table=[];
      this.masami.table=[];
      
      items.forEach(item => {
        if(item.personal_name == 'is') {
          this.issei.table.push({
            // date: item.register_date,
            date: this.formatDate(item.register_date),
            fee: item.register_fee
          });
        }
        else if(item.personal_name == 'ms') {
          this.masami.table.push({
            // date: item.register_date,
            date: this.formatDate(item.register_date),
            fee: item.register_fee
          });
        }
      });      
    },
    
    async createBooks(name, fee) {
      const data = {
        personal_name: name,
        register_date: new Date(),
        register_fee: fee
      };
      
      await API.graphql({
        query: createBooks,
        variables: { input: data },
      })
      .then((result) => {
        console.log(result);
      })
      .catch((error) => {
        console.log(error);
      });
      
      await this.setTable();
    },
    
    async clickSubmitFreeIncome() {
      if(this.add==null || Number(this.add)==0) {
        return;
      }
      
      this.$emit('loading', true);
      
      const updateData = {
        id: this.selectData.id,
        total: this.selectData.total + Number(this.add)
      };
      
      await API.graphql({
        query: updatePersonal,
        variables: {
          input: updateData
        }
      })
      .then((result) => {
        console.log(result);
        this.createBooks(this.selectData.name, Number(this.add));
        this.selectData.total += Number(this.add);
        this.add = null;
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {
        this.$emit('loading', false);
        this.freeIncomeDialog = false;
        this.setTable();
      });
    },
    
    async clickSubmitMoveIncome() {
      
      if(this.add==null || Number(this.add)==0) {
        return;
      }
      
      if(this.selectData.total_wash == 0) {
        this.moveIncomeDialog = false;
        return;
      }
      
      this.$emit('loading', true);
      
      const updateData = {
        id: this.selectData.id,
        total: this.selectData.total + Number(this.add),
        total_wash: this.selectData.total_wash - Number(this.add)
      };
      
      await API.graphql({
        query: updatePersonal,
        variables: {
          input: updateData
        }
      })
      .then((result) => {
        console.log(result);
        this.createBooks(this.selectData.name, Number(this.add));
        this.selectData.total += Number(this.add);
        this.selectData.total_wash -= Number(this.add);
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {
        this.$emit('loading', false);
        this.moveIncomeDialog = false;
        this.add = null;
        this.add_part = null;
        this.setTable();
      });
    },
    
    async clickSubmitOutgo() {
      if(this.remove==null || Number(this.remove)==0) {
        return;
      }
      
      this.$emit('loading', true);
      
      const updateData = {
        id: this.selectData.id,
        total: this.selectData.total - Number(this.remove)
      };
      
      await API.graphql({
        query: updatePersonal,
        variables: {
          input: updateData
        }
      })
      .then((result) => {
        console.log(result);
        this.createBooks(this.selectData.name, -Number(this.remove));
        this.selectData.total -= Number(this.remove);
        this.remove = null;
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {
        this.$emit('loading', false);
        this.outgoDialog = false;
        this.setTable();
      });
    }
  },
  
  watch: {
    tab: function(val) {
      if(val==0) {
        this.selectData = this.issei;
      }
      else if(val==1) {
        this.selectData = this.masami;
      }
    },
    
    add_part: function(val) {
      if(Number(val)) {
        this.add = val;
      }
    }
  }
};
</script>

